import * as React from "react";

interface Props { onHandleInputChange?: any; }
interface Model { [key: string]: any; HomePhone?: any; Phone?: any; Code?: any; Mailbox?: any; Address?: any; ContactsName?: any; ContactsPhone?: any; }
interface State { check: Model; Model: Model }

// 
export class Contact extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            check: {}, Model: {
                HomePhone: "",
                Phone: "",
                Code: "",
                Mailbox: "",
                Address: "",
                ContactsName: "",
                ContactsPhone: "",
            }
        };
        this.handleChange = this.handleChange.bind(this);
    }
    componentDidMount() {
        this.props.onHandleInputChange("Contact", this.state.Model);
    }
    handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState(prevState => {
            prevState.Model[name] = value;
        });
        this.props.onHandleInputChange("Contact", this.state.Model);
        this.check(name, value);
    }
    check(name: any, value: any) {
        this.setState(prevState => {
            // console.log(prevState);
            prevState.check[name] = true;
        });
    }
    render() {
        return <div>
            <div className="container">
                <div className="page-header">
                    <h1></h1>
                </div>
                <div className="form-horizontal">
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                    <div className="form-group col-sm-12 col-lg-12">
                        <label className="col-sm-2 control-label"></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                </div>

            </div>
            <div className="container">
                <div className="page-header">
                    <h1></h1>
                </div>
                <div className="form-horizontal">
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                    <div className="form-group col-sm-12 col-lg-6">
                        <label className="col-sm-4 control-label"></label>
                        <div className="col-sm-8">
                            <input type="text" className="form-control" />
                        </div>
                    </div>
                </div>
            </div></div>
            ;
    }
}


